@import "../../utils/style/variables.scss";

.property_container {
    .property_content {
        display: flex;
        flex-direction: column;
        gap: 15px;
        & > header {
            color: $primary;
            position: relative;
            padding: 10px 0 15px 0;
            h1 {
                width: 80%;
                font-size: clamp(1.8rem, 3vw, 3.6rem);
                margin-bottom: 7px;
                @media (min-width: $desktop) {
                    margin: 20px 0 5px 0;
                }
            }
            .location {
                font-size: clamp(1.4rem, 2.2vw, 1.8rem);
                font-weight: 500;
            }
            .tags_container {
                display: flex;
                gap: 10px;
                margin: 10px 0 30px 0;
                @media (min-width: $tablett) {
                    margin: 20px 0 0 0;
                }
                .tag {
                    border-radius: 5px;
                    padding: 3px 15px;
                    background-color: $primary;
                    color: #ffffff;
                    font-size: clamp(1rem, 1.6vw, 1.4rem);
                    font-weight: 500;
                    @media (min-width: $tablett) {
                        border-radius: 10px;
                    }
                }
            }
            .identity_container {
                position: absolute;
                bottom: 5%;
                right: 5%;
                display: flex;
                gap: 5px;
                align-items: center;
                @media (min-width: $tablett) {
                    bottom: 85%;
                    right: 0;
                    transform: translateY(100%);
                }
                .host_name {
                    text-align: right;
                    font-size: clamp(1.2rem, 1.8vw, 1.8rem);
                    padding: 0 5px;
                }
                img {
                    width: clamp(40px, 8vw, 70px);
                    height: clamp(40px, 8vw, 70px);
                    border-radius: 50%;
                }
            }
        }
        .collapse_wrapper {
            @extend .property_content;
            @media (min-width: $desktop) {
                flex-direction: row;
                gap: 60px;
            }
        }
    }
    .error_message {
        @include generic_message;
    }
}
